<template>
  <div className="animated">
    <b-card>
      <b-card-header>
        <i class="icon-menu mr-1"></i>Product List
      </b-card-header>
      <b-card-body>
        <v-client-table :columns="columns" :data="data"  :options="options" :theme="theme" id="dataTable">
          <router-link slot="_id" slot-scope="props" target="_blank" :to="`product/${props.row._id}`" class="icon-eye"></router-link>
        </v-client-table>
      </b-card-body>
    </b-card>

    <router-link :to="{ name: 'Create Product' }">
      <b-button type="submit" value="Add Product" variant="outline-dark"> Add Product </b-button>
    </router-link>
  </div>
</template>
<script>
import { Callout } from '@coreui/vue'
  import Vue from 'vue'
  import {ClientTable, Event} from 'vue-tables-2'
  import axios from 'axios'


  Vue.use(ClientTable)

  export default {
    name: 'dashboard',
    components: {
      ClientTable,
      Event,
      
    },
    data: function () {
      return {
        columns: ['product_name','price','short_description', 'long_description','_id'],
        data: [],
        options: {
          headings: {
            product_name : 'Product Name',
            price : 'Price',
            short_description : 'Short Description',
            long_description : 'Long Description',
            _id : 'URL'
          },
          sortable: ['product_name','price','short_description', 'long_description'],
          filterable: ['product_name','price','short_description', 'long_description'],
          sortIcon: { base:'fa', up:'fa-sort-asc', down:'fa-sort-desc', is:'fa-sort' },
          pagination: {
            chunk: 5,
            edge: false,
            nav: 'scroll'
          }
        },
        useVuex: false,
        theme: 'bootstrap4',
        template: 'default'
      }
    },
    async mounted() {
    axios.get("https://selacious-cloud-siteapi.herokuapp.com/products")
    .then(response => {this.data = response.data})
  },
  };

// </script>

